:root {
    --app-background-dar: rgb(24, 23, 32);
}

body {
    background-color: var(--app-background-dar);
    font-family: 'Inter', sans-serif;
    height: 100vh;
    overflow: hidden;
    margin: 0;
    padding: 0;
}

.container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: min(100% - 30px, 1200px);
    margin-inline: auto;
}

main {
    padding: 60px 0;
}

section {
    margin-top: 60px;
}

h1, h2, h3, p, li, a, input {
    color: #fff;
}

h2 {
    font-size: 2.2rem;
}

p {
    margin-top: 15px;
    font-size: 1.8rem;
}

main {
    padding: 30px 0;
}

h2 {
    font-size: 2rem;
    font-weight: 400;
    line-height: 1;

    span {
        color: #00f3b6;
    }
}

input {
    width: 140px;
    height: 34px;
    margin-top: 10px;
    padding: 0 20px;
    color: #000;
    border-radius: 30px;
    border: 1px solid   rgb(255 255 255 / 5%);
    background-color: 1px solid #8c83830d;
    font-size: 1.8rem;
    text-align: center;
}